<template>
  <v-container
    id="dashboard-view"
    fluid
    tag="section"
  >
    <v-row justify="center">
      <v-col cols="12">
        <material-card
          color="primary"
          icon="mdi-wallet"
        >
           <template #title>
            <h2>ALL TRANSACTIONS</h2>
          </template>
          <v-container class="py-0">
            <v-data-table
              :headers="headers"
              :items="transactions"
              class="elevation-1"
              loading-text="Loading... Please wait"
              v-if="search==''"
            >
              <!--verified or not-->
              <template v-slot:item.status="{ item }">
                <v-chip
                  v-if="item.status==1"
                 color="green"
                  label
                >
                  Verified
                </v-chip>
                <v-chip
                  v-else
                   color="warning"
                  label
                >
                  Not verified
                </v-chip>
              </template>
            </v-data-table>
            <!--------------search campaigns---------------------->
          </v-container>
        </material-card>
      </v-col>
    </v-row>

    <!-- Modals -->
    <!-- Approve Campaign dialogue-->
    <v-dialog
      v-model="approveModal"
      transition="dialog-top-transition"
      max-width="400"
    >
      <v-card max-width="400">
        <v-toolbar
          color="success"
          dark
        >
          <v-icon>mdi mdi-warning </v-icon> {{modalText.headerText}}
          <v-btn
            icon
            absolute
            right
            outlined
            class="float-right"
            color="white"
            @click="approveModal = false"
          >
            <v-icon>mdi mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card-text>
          <p class="pt-4 text-center text-h3">
           {{modalText.questionText}} <br>
          </p>
        </v-card-text>
        <v-card-actions class="justify-center">
          <v-btn
            color="success"
            @click="approve()"
          >
            {{modalText.actionText}} <v-icon>mdi-check-outline</v-icon>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <!--Delete Modal-->
  </v-container>
</template>

<script>
import {mapGetters} from 'vuex';
import axios from 'axios';
  let digitadApps={
    admin:'https://portal.digitadooh.com',//https://digitad.us-east-2.elasticbeanstalk.com
     users: "https://app.digitadooh.com",
    piApp: "https://ourcore.digitadooh.com",
    storage:'https://storage.digitadooh.com'
  }
  export default {
    name: 'DashboardView',

    data: () => ({
      id: '',
      search:'',
      approveModal: false,
      deleteModal:false,
      headers: [
        { text: 'ID', value: 'id' },

        {
          text: 'Reference',
          align: 'start',
          value: 'reference'
        },
        { text: 'Method', value: 'method' },
        { text: 'Operator', value: 'operator' },
        { text: 'Status', value: 'status' }
      ],
      currentCampaign:{},
      modalText:{
        headerText:"",
        actionText:"",
        questionText:""
      }
    }),
    methods: {
      debit(item){
        //console.log(this.currentCampaign);
        let formData = new FormData();
        let data_param = JSON.stringify(item);
        formData.append("data",data_param);
        const params = { crossDomain: true, withCredentials: false }; //http://localhost/digit-ad/dashboard/campaign/submit_campaign.php
        const api = axios.create({ baseURL:digitadApps.admin });
        api
          .post(
            digitadApps.admin+"/server/debitClient.php",
            formData
          )
          .then((feedback)=>{
            if(feedback.data==1){
              alert('Debit Successful');
              location.reload();
            }else if(feedback.data==2){
              alert('Campaign has not been appoved!');
            }else{
              alert('Debit Failed');
              console.log(feedback.data)
            }
          })
          .catch((error)=>{
            console.log(error);
          })
      },
      approve () {
        //console.log(this.currentCampaign);
        let formData = new FormData();
        let data_param = JSON.stringify(this.currentCampaign);
        formData.append("data",data_param);
        const params = { crossDomain: true, withCredentials: false }; //http://localhost/digit-ad/dashboard/campaign/submit_campaign.php
        const api = axios.create({ baseURL:digitadApps.admin });
        api
          .post(
            digitadApps.admin+"/server/toggle_campaign_approval.php",
            formData
          )
          .then((result) => {
            console.log(result.data);
            if (result.data == 1) {
              //check operation and render message
              if(this.currentCampaign.approve==true){
                //campaign approval
                this.modalText.questionText="Successful!";
                this.modalText.actionText="Approved!";
                this.activeCampaigns.push(this.currentCampaign);
                location.reload();
              }else if(this.currentCampaign.approve==false){
                 //campaign rejection
                this.modalText.questionText="Successful!";
                this.modalText.actionText="Rejected!";
                this.unApprovedCampaigns.push(this.currentCampaign);
                location.reload();
              }
              
            }
          })
          .catch((error) => {
            console.log(error);
          });
      },
      pushSearch(){
        this.allCampaigns.forEach(campaign => {
         //console.log(campaign.name.toLowerCase().indexOf(this.search.toLowerCase()));
         //search keyword in all campaigns
          if(campaign.name.toLowerCase().indexOf(this.search.toLowerCase())!=-1||campaign.campaignCountry.toLowerCase().indexOf(this.search.toLowerCase())!=-1){
            //check if  this.searchMatches is empty or not
            if( this.searchMatches.length!=0){
              //check if search already in search store
                this.searchMatches.forEach(new_campaign=>{
               if(new_campaign.name.toLowerCase().indexOf(campaign.name.toLowerCase())==-1||new_campaign.campaignCountry.toLowerCase().indexOf(campaign.campaignCountry.toLowerCase())==-1){
                  this.searchMatches.push(campaign);
               }
             })
            }else{
              this.searchMatches.push(campaign);
            }
          }
        });
      },
      setModalText(item){
        let item_approve_code=item.approve;
        if(item_approve_code==true){
          this.modalText.headerText="Approve Campaign";
           this.modalText.actionText="Approve";
            this.modalText.questionText="Are you sure you want to approve this campaign?";
        }else if(item_approve_code==false){
           this.modalText.headerText="Reject Campaign";
           this.modalText.actionText="Reject";
            this.modalText.questionText="Are you sure you want to reject this campaign?";
        }else{
           this.modalText.headerText="Delete Campaign";
           this.modalText.actionText="Delete";
            this.modalText.questionText="Are you sure you want to delete this campaign?";
        }
      },
      deleteCampaign(){
        let campaignToDelete = this.currentCampaign;
        let user_id = campaignToDelete.user_id;
        let campaign_name = campaignToDelete.media;
        var user = piCredentials.username;
        var password = piCredentials.password;
        var base64encodedData = Buffer.from(user + ":" + password).toString(
          "base64"
        );
        //delete file from pisignage
        var config = {
          method: "delete",
          url: digitadApps.piApp + "/api/files/" + campaign_name,
          headers: {
            "Content-Type": "application/json",
            Authorization: `Basic ${base64encodedData}`,
          },
        };

        axios(config)
          .then(function(response) {
            console.log(JSON.stringify(response.data));
            if (response.data.success == true) {
              let formDataK = new FormData();
              let data_paramX = JSON.stringify(campaignToDelete);
              formDataK.append("data", data_paramX);
              const apiX = axios.create({ baseURL: digitadApps.storage });
              apiX
                .post(digitadApps.storage + "/deleteCampaign.php", formDataK)
                .then((resultK) => {
                  console.log(resultK.data);
                  if (resultK.data == 1) {
                    //campaign delete
                    //location.reload();
                    //Udate database
                        let formData = new FormData();
                        let data_param = JSON.stringify(campaignToDelete);
                        formData.append("data", data_param);
                        formData.append("user_id", user_id);
                        const api = axios.create({ baseURL: digitadApps.users });
                        api
                          .post(digitadApps.users + "/server/deleteCampaign.php", formData)
                          .then((result) => {
                            console.log(result.data);
                            if (result.data == 1) {
                              //campaign delete
                              location.reload();
                            } else {
                              alert("Failed to delete!");
                            }
                          })
                          .catch((error) => {
                            console.log(error);
                          });
                    ////update database end
                  } else {
                    alert("Failed to delete!");
                  }
                })
                .catch((error) => {
                  console.log(error);
                });
              //delete campaign file from storage app
              //update users platform
              /**/
            } else {
              alert("Failed to delete campaign");
            }
          })
          .catch(function(error) {
            console.log(error);
          });
      },
      campaignDetails (item) {
        let id=item.id;
        this.viewedCampaign.push(item);
        this.$router.push(`/campaign/${id}`);
      },
      setCurrentCampaign(campaign){
        this.currentCampaign=campaign; 
      }
    },
    computed:mapGetters(['viewedCampaign','allCampaigns', "activeCampaigns",
    "archivedCampaigns",
    "unApprovedCampaigns",
    "not_paid_campaigns","searchMatches","transactions"]),
   
  }
</script>
<style>
.card-elm:hover {
  border: 2px solid orange;
}
.map {
  background-image: url("https://www.nationsonline.org/maps/germany-administrative-map.jpg");
  background-color: #cccccc;
  background-size: 100%;
  width: 100%;
  min-height: 700px;
}
.v-dialog--fullscreen {
  margin-top: 3% !important;
  margin-left: 0;
  margin-right: 0;
  margin-bottom: 0;

  /* margin: 5.2%; */
}
.cursor {
  cursor: pointer;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
.task-btn{
  margin:10px
}
</style>
